'use client';
import { useEffect, useRef } from 'react';

export default function Page() {
  const h1Ref = useRef<HTMLHeadingElement>(null);
  const divRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    // 类和对象
    class Car {
      brand: string;
      model: string;
      year: number;
      constructor(brand: string, model: string, year: number) {
        this.brand = brand;
        this.model = model;
        this.year = year;
      }
      start() {
        console.log(`汽车 ${this.brand} ${this.model} 启动了`);
      }
      stop() {
        console.log(`汽车 ${this.brand} ${this.model} 停止了`);
      }
    }
    const myCar = new Car('Mi', 'Yu7', 2025);
    myCar.start();
    myCar.stop();

    class ElectricCar extends Car {
      range: number;
      constructor(brand: string, model: string, year: number, range: number) {
        super(brand, model, year);
        this.range = range;
      }
      charge() {
        console.log(`电动汽车 ${this.brand} ${this.model} 正在充电`);
      }
    }
    const myElectricCar = new ElectricCar('Tesla', 'Model Y', 2025, 300);
    myElectricCar.start();
    myElectricCar.charge();

    // 回调函数练习
    console.log('111111');
    setTimeout(function () {
      console.log('22222');
    }, 2000);
    console.log('333333');

    // DOM 操作
    if (divRef.current) {
      console.log(divRef.current);
      divRef.current.innerHTML = '这是从脚本动态更改的内容';
      divRef.current.onclick = function () {
        alert('我也被点击了！');
      };
    }
  }, []);

  return (
    <div className="practice-html">
      <h1 ref={h1Ref} style={{ cursor: 'pointer' }} onClick={() => alert('我被点击了！')}>
        JavaScript 语法
      </h1>
      <div ref={divRef}>div content</div>
    </div>
  );
} 